form {
    text-align: center;
}

.loginInput {
    width: 50%;
}

label {
    font-size: larger;
    font-weight: 500;
    color: 233D4D;
    padding-bottom: 20px;
    float: left;
}

textarea {
    width: 101.5%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: medium;
}

input {
    font-size: medium;
    width: 100%;
    padding: 10px;
}

.selectInput {
    font-size: medium;
    width: 102%;
    padding: 10px;
}

.required:after {
    content: ' * ';
    color: red;
}

.inputDiv {
    padding: 15px 20px 15px 15px
}

img {
    vertical-align: middle;
    padding-right: 10px;
}

.coolButton {
    display:inline-block;
    font-size: medium;
    background-color: #FFFFFF;
    padding:0.35em 1.2em;
    border:0.1em solid #000000;
    margin:0 0.3em 0.3em 0;
    border-radius:0.12em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color:#000000;
    text-align:center;
    transition: all 0.2s;
}

.coolButton:hover{
    color:#FFFFFF;
    background-color:#000000;
}

.tagButton {
    margin: 15px 5px 0px 0px
}

.eventButton {
    margin: 20px 0px 0px 0px
}

::-webkit-input-placeholder {
    font-style: italic;
 }
 :-moz-placeholder {
    font-style: italic;  
 }
 ::-moz-placeholder {
    font-style: italic;  
 }
 :-ms-input-placeholder {  
    font-style: italic; 
 }

 .show-error {
     opacity: 1;
     transition: all 250ms linear;
 }

 .hide-error {
     opacity: 0;
     transition: all 250ms linear;
 }